<template>
    <el-dialog
    title="详情展示"
    :visible.sync="isNewsadd"
    width="40%"
    center="center"
    :before-close="handleClose">
    <div><h3>{{Newsdata.title}}</h3></div>
    <div><h5><i class="el-icon-time"></i>{{Newsdata.addtime}}</h5></div>
    <div>
            <el-image
            style="width: 400px; height: 600px"
            :src="img+Newsdata.picture"
            fit="fill"></el-image>
    </div>
    <div><h5>{{Newsdata.content}}</h5></div>
    </el-dialog>
</template>

<script>
export default {
    props:{
        isNewsadd:{
            type:Boolean,
            default:false
        },
        Newsdata:{
            type:Object,
            default:()=>{}
        }
    },
    data(){
        return{
            img:'http://estate.eshareedu.cn/estate/upload/',
        }
    },
     methods: {
        
        // 添加
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(() => {
            // done();
            this.$emit('change',false)
          })
          .catch(() => {});
      }
    }
}
</script>