<template>
    <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="65%"
    center="center"
    :before-close="handleClose">
    <el-form ref="form" :model="form" label-width="95px">
        <el-form-item label="标题：" style="width:50%;">
            <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="公告内容：">
            <editor-vue 
            ref="editor"
            :content="content"
            :readOnlys="readOnlys"
            @changeData="hChangeData"
            />
        </el-form-item>
        <el-form-item label="公告图片：">
            <el-upload
                class="avatar-uploader"
                action="http://estate.eshareedu.cn/estate/api/upload/add"
                :headers="headers"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
        </el-form-item>
        <el-form-item label="状态：">
                <el-radio v-model="form.state" :label="1">开始</el-radio>
                <el-radio v-model="form.state" :label="0">停止</el-radio>
        </el-form-item>
        <el-form-item label="类型：">
            <el-select v-model="form.type" placeholder="请选择">
                <el-option v-for="(imet,index) in type" :key="index" :label="imet.name" :value="imet.id"></el-option>
            </el-select>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="serate">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
    </span>
    </el-dialog>
</template>
<script>
import EditorVue from '../News/editorVue.vue'
export default {
    props:{
        dialogVisible:{
            type:Boolean,
            default:false
        },
        form:{
            type:Object,
            default:()=>{}
        },
        title:{
            type:String,
            default:'添加公告'
        }
    },
    data(){
        return{
            headers:{Authorization:sessionStorage.getItem('token')},
                  
            query:{
                page:1,
                psize:5
            },
            type:[],
            imageUrl: '',
            readOnlys: false,
            content: ``,
            img:"http://estate.eshareedu.cn/estate/upload/"
        }
    },
    components:{
        EditorVue
    },
    created(){
        this.gettype()
        // this.getnews()
    },

    // updated(){
    //     this.title = this.modcom.id?'修改公共':'公告添加'
    //     this.form = this.modcom?this.modcom:{}
        
    //     if(this.title == '修改公共'){
    //         this.imageUrl = "http://estate.eshareedu.cn/estate/upload/"
    //     }else{
    //         return this.imageUrl
    //     }    this.title=='修改公共'?imageUrl+this.form.picture:
        
    // },

    methods:{
        // // 单条信息
        // async getnews(){
        //     let res = await this.http.get('/news/get',{id:this.form.id})
        //     // console.log(res)
        //     if(res.errCode == 10000){
        //         console.log(this.$refs)
        //     }
        // },

        // 确定
        async submit(){
            
            // this.form.state = this.form.state == '确定'?1:0
            this.form.content = this.content
            this.form.edituser = this.$store.state.username
            this.form.addtime = new Date().toLocaleString()
            // console.log(this.form)
            let res = await this.http.post('/news/add',this.form)
            // console.log(res)
            if(res.errCode == 10000){
                this.$message.success('添加成功')
                this.$emit('change',false)
                this.imageUrl = ""
                this.$refs.editor.html = ""
            }
        },
        // 文本域
        hChangeData(editDataHtml){
        // 获取最新的html数据
        this.content = editDataHtml
        // this.content = this.content.replace(/<[/!][^<>]>/gi, '')
        console.log(this.content)
        },
        hUploadImg(file,insertFn){
        console.log(file)
        // 插入图片，调接口返回图片url,通过插入conteng
        let imgUrl = 'https://img1.baidu.com/it/u=608404415,1639465523&fm=253&fmt=auto&app=120&f=JPEG?w=1280&h=800'
        insertFn(imgUrl);

        // 设置只读
        this.readOnlys = true;      
        },
        // 图片
        handleAvatarSuccess(res, file) {
            this.form.picture = res.data
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        // 公告类型
        async gettype(){
            let res = await this.http.get('/newstype/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.type = res.data.list
            }
        },
        // 取消
        serate(){
            this.$emit('change',false)
                this.$refs.editor.html = ""
                this.imageUrl = ""
        },
        // 添加
        handleClose(done) {
        this.$confirm('确认关闭？')
          .then(() => {
            // done();
            this.$emit('change',false)
                this.$refs.editor.html = ""
                this.imageUrl = ""
          })
          .catch(() => {});
      },
        async getdetail(id){
            let res = await this.http.get('/news/get',{id:id})
            this.$refs.editor.html = res.data.content
        }
    },
    watch:{
        form:{
            handler(newval){
                // console.log(newval)
                this.$nextTick(()=>{
                    if(newval.id){
                        this.imageUrl = this.img + newval.picture
                        this.getdetail(newval.id)
                    }
                })
                
            },
            deep:true
        }
    }
}
</script>

<style lang="less" scoped>
.el-form{
    display: block;
    /deep/.el-input__inner{
        border: 1px solid #ccc !important;
    }
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>