<template>
    <div>
    <el-card class="box-card">
      <span>公告管理</span>
    </el-card>
    <el-row>
      <el-col :span="24">
        <el-form ref="form" :model="query" label-width="95px">
          <el-form-item label="新闻：" style="width: 18%">
            <el-input v-model="query.key" placeholder="新闻名" @change="sreatech" clearable @keyup.native.enter="sreatech"></el-input>
          </el-form-item>
          <el-form-item label="公告类型：">
            <el-select v-model="query.type" placeholder="请选择" @change="sreatech" clearable @keyup.native.enter="sreatech">
            <el-option v-for="(imet,index) in list" :key="index" :label="imet.name" :value="imet.id"></el-option>
            </el-select>
        </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="sreatech">查询</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row class="butt">
      <el-button type="primary" @click="add" >添加</el-button>
      <el-button type="danger" @click="del">批量删除</el-button>
    </el-row>

    <!-- 表格 -->
          <Table
          :loading="loading"
          :tableData="tableData"
          :tableCouml="tableCouml"
          @idshow="idshow">

          <template #title="data">
            <a @click="deladd(data.data)" href="javascript:void(0);">{{data.data.title}}</a>
          </template>

          <template #imgage="data">
            <el-image
            style="width: 100px; height: 100px"
            :src="img+data.data.picture"
            fit="fill"></el-image>
          </template>

          <template #state="data">
              <el-switch
                v-model="data.data.state"
                :active-value=1
                :inactive-value=0
                @change="changeserage(data.data)"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
          </template>

          <template #option="data">
            <el-button size="mini" type="success" @click="mod(data.data)">修改</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(data.data)">删除</el-button>
          </template>
    </Table>
      <!-- 分页 -->
          <Paging :query="query" :total="total" @paging="paging"></Paging>
        <!-- 详情 -->
        <Newsadd :isNewsadd="isNewsadd" @change="change" :Newsdata="Newsdata"></Newsadd>
        <!-- 添加 -->
        <Newstian :dialogVisible="dialogVisible" @change="change" :form="form" :title="title"></Newstian>
  </div>
</template>
<script>

import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
import Newsadd from '@/components/News/Newsadd.vue';
import Newstian from '@/components/News/Newstian.vue'

export default {
          data() {
    return {
      form:{
        state:1,
        id: 0,
        hits: 0,
        },
      loading: true,
      list:[],
      query:{
        page:1,
        psize:5
      },
      tableData: [],
      tableCouml: [
                { prop: "id", label: "ID",},
                { prop: "typename", label: "类型",},
                { prop: "title", label: "标题", isshow:true,showname: "title"},
                { prop: "picture", label: "图片", isshow:true,showname: "imgage"},
                { prop: "state", label: "状态",  isshow:true,showname: "state"},
                { prop: "addtime", label: "发布时间",},
                { prop: "edituser", label: "发布人",},
                { label: "操作", isshow: true, showname: "option" },
            ],
      total:0,
      img:'http://estate.eshareedu.cn/estate/upload/',
      lt:[],
      isNewsadd:false,
      dialogVisible: false,
      Newsdata:{},
      ids:[],
      modcom:{},
      title:'公告添加',
    };
  },
  components:{
    Paging,
    Table,
    Newsadd,
    Newstian
  },
  created(){
    this.getimet()
    this.gettypename()
  },
  methods:{
    // 添加
    add(){
      this.dialogVisible = true
      this.form = {state:1,id: 0,hits: 0,}
      this.title='公告添加'
    },
    // 修改
    mod(val){
      this.form = val
      // console.log(val)
      this.dialogVisible = true
      this.title='修改公告'
    },
    // 查询
    sreatech(){
      this.getimet()
    },
    // 详情
    deladd(data){
      // console.log(data)
      this.isNewsadd = true
      this.Newsdata = data
    },
    change(val){
      this.isNewsadd = val
      this.dialogVisible = val
      this.getimet()
      this.modcom = {}
    },
    // 按钮状态
    async changeserage(data){
      // console.log(data)
      let res = await this.http.post('/news/add',data)
      if(res.errCode == 10000){
        this.$message.success('更改成功')
      }
    },
    // 刷新页面
        paging() {
            this.getimet();
        },
    // 公告类型
    async gettypename(){
        let res = await this.http.get('/newstype/list')
        // console.log(res)
        if(res.errCode == '10000'){
            this.list = res.data.list
        }
    },
    // 批量删除
    idshow(val){
      this.ids.push(val)
    },
    async del(){
      let res = await this.http.post('/news/deleteall',{ids:this.ids})
      if(res.errCode == 10000){
        this.$message.success('删除成功')
        this.getimet()
      }
    },
    // 删除
     handleDelete(val){
       this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
            let res = await this.http.get('/news/delete',{id:val.id})
            if(res.errCode == '10000'){
                this.$message.success('删除成功')
                this.getimet()
            }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
      // 列表
      async getimet(){
        let res = await this.http.get('/news/list',this.query)
        // console.log(res)
        if(res.errCode == '10000'){
          this.tableData = res.data.list
          this.total = res.data.counts
          this.loading = false
        }
      },
  },
}
</script>

<style lang="less" scoped>
@import '../assets/css/admin.css';
a{
  text-decoration: underline;
}
/deep/.el-icon-plus{
  border: 1px solid #ccc !important;
}
</style>